/*------------------------------------*\
 **************************************
 * FORMS
 **************************************
\*------------------------------------*/

// Remove input-styles from iOS
input {
  &[type='text'],
  &[type='email'],
  &[type='password'] {
    -webkit-appearance: none;
  }
}

textarea {
  -webkit-appearance: none;
}

// Style placeholder text in inputs
input::-webkit-input-placeholder {
color: $grey !important;
}

input:-moz-placeholder { /* Firefox 18- */
color: $grey !important;
}

input::-moz-placeholder {  /* Firefox 19+ */
color: $grey !important;
}

input:-ms-input-placeholder {
color: $grey !important;
}


/*------------------------------------*\
 * FORM GROUP
\*------------------------------------*/

.form-inline {
  .a-form-group {
    padding-right: $spacer;
  }
}

.a-form-group {
  position: relative;

  // Form group items (Container for Label and Input)
  .a-form-group-items,
  .input-group {
    position: relative !important;

    &.transparent {
      input,
      button,
      .input-group-addon {
        background-color: transparent;
      }

      input {
        &:-webkit-input-placeholder {
          color: $grey;
        }

        &:-moz-placeholder {
          color: $grey;
        }

        &:-ms-input-placeholder {
          color: $grey;
        }
      }
    }
  }

  // Label
  .a-form-label-inline {
    @extend label;
    display: inline-block;
    margin-right: $spacer * .5;
    font-family: $font-medium;
    font-weight: $font-weight-medium;
    line-height: $spacer * 3;
  }

  .a-form-label {
    display: block;
    margin-bottom: $spacer / 2;
    font-family: $font-medium;
    font-size: 16px;
    font-weight: $font-weight-medium;
  }

  // Input
  .form-control {
    height: $spacer * 3;
    padding: 5px $spacer 3px $spacer;
    margin-left: -$spacer / 12;
    color: $grey;
    border: 2px solid $blue;
    border-radius: 0;

    &:focus {
      border: 2px solid $blue-dark;
    }
  }

  // Button right to input
  .input-group-btn {
    .btn {
      width: $spacer * 4;
      height: $spacer * 3;
      padding: 0;
      padding-top: $spacer / 3;
      padding-right: 6px;
      color: $black;
      text-align: right;
      border: 2px solid $blue;
      border-left: none;

      &:hover,
      &:active,
      &:focus {
        background: $blue-light;
        outline: none;
      }
    }
  }

  // Form group with LARGE input field
  &.a-form-group-large {
    .form-control {
      height: $spacer * 5;
      padding: 13px $spacer 10px $spacer;
      font-size: 20px;
    }

    .input-group-btn {
      .btn {
        width: $spacer * 5;
        height: $spacer * 5;
        padding-right: $spacer;
      }
    }

    i {
      top: $spacer;
      left: 14px;
      font-size: 30px;
    }

    .input-group-addon {
      padding: 18px $spacer;
      font-size: 20px;
    }
  }

  // VALIDATOR: Show help message if error
  .a-message {
    display: none;
    margin-top: $spacer;
  }

  .a-message-absolute {
    position: absolute;
    top: $spacer * 5;
  }

  &.has-error {
    .a-message {
      display: block;
      margin-bottom: $spacer * 2;
      clear: both;
      position: absolute;
      z-index: 9999;
    }
  }

  // VALIDATOR: Icon, Display icon only when feedback
  .form-control-feedback {
    position: absolute;
    right: $spacer;
    bottom: 8px;
    z-index: 9999;
    display: none;
    font-size: 20px;
    text-align: center;
    pointer-events: none;

    &.glyphicon-ok,
    &.glyphicon-remove {
      display: block;
    }
  }

  // VALIDATOR: Input has error
  &.has-danger {
    // Label
    .input-group-addon {
      border: 2px solid $red !important;
    }

    // Input
    .form-control {
      border: 2px solid $red !important;
    }

    // Icon
    .form-control-feedback {
      &::before {
        content: '\f00f';
      }
    }

    // When inside input-group
    .input-group {
      .input-group-addon {
        border-right: none !important;
      }

      .form-control {
        border-left: none !important;
      }
    }
  }

  // VALIDATOR: Input has success
  &.has-success {
    .input-group-addon {
      border: 2px solid $green !important;
    }

    // Input
    .form-control {
      border: 2px solid $green !important;
    }

    // When inside input-group
    .input-group {
      .input-group-addon {
        border-right: none !important;
      }

      .form-control {
        border-left: none !important;
      }
    }
  }

  // INPUT GROUPS (Putting label inside of textarea)
  .input-group {
    // Label
    .input-group-addon {
      min-width: 44px;
      height: $spacer * 3;
      padding: 9px $spacer / 2 $spacer $spacer;
      margin-bottom: 0;
      font-size: 16px;
      font-weight: normal;
      line-height: 1;
      color: #000;
      text-align: left;
      background-color: $white;
      border: 2px solid $blue;
      border-right: none;
      border-radius: 0;

      i {
        margin-top: $spacer / 3 * -1;
        // margin-left: $spacer / 2 * -1;
        vertical-align: middle;
      }

      &.a-input-group-addon-phone {
        padding: 7px 6px 12px 12px;
        i {
          margin-left: $spacer / 2 * -1;
        }
      }
      &.a-input-group-addon-date {
        padding: 7px 6px 12px 12px;
        i {
          margin-top: -3px;
        }
      }
    }

    // input: Hide border left if there is a addon
    .form-control {
      &:not(.form-control-noAddon) {
        padding-left: 0;
        border-left: none;
      }
    }

    // If there is buttons inside the input group..
    .a-form-control-withBtn {
      border-right: none;
    }

    // Validator message
    &+.a-message {
      margin-top: $spacer;
    }

    // Focus state on label and input
    // JS: If state on input is "focus",
    // add class to input-group : "a-input-focus".
    // added setting background to white here, for input-fields that are transparent, but should be white when active.
    &:hover,
    &.a-input-focus {
      .input-group-addon,
      .form-control,
      .input-group-btn .btn {
        z-index: 4;
        background-color: $white;
        border-color: $blue-dark;
      }
    }

    // Disabled input
    &.disabled {
      .input-group-addon,
      .form-control {
        color: $grey;
        background-color: $white;
        background-image: url('../images/disabled-background.svg');
      }

      .input-group-addon {
        border: 2px solid $grey-medium;
        border-right: none;
      }

      // Input
      .form-control {
        border: 2px solid $grey-medium;
        border-left: none;
      }
    }
  }

  // Approved input
  .disabled {
    &.a-input-approved {
      .input-group-addon,
      .form-control {
        color: $black !important;
        background-color: #c8e9d6;
        background-image: url('../images/approved-background.svg');
        border: none !important;
      }
    }
  }

  /*------------------------------------*\
   * TEXT AREA
  \*------------------------------------*/
  .a-textarea {
    min-height: $spacer * 11;
    padding: $spacer;
    border: 2px solid $blue;
    border-radius: 0;

    &:hover,
    &:focus {
      border: 2px solid $blue-dark;
      box-shadow: none !important;
    }
  }
}


/*------------------------------------*\
 * FORM HELP TEXT / VALIDATOR INFO
\*------------------------------------*/

.a-validatorInfo {
  margin-top: $spacer;
  font-size: 14px;
  color: $black;
}

.a-validatorInfo-icon {
  margin-top: $spacer / 6 * -1;
  color: $blue;
  vertical-align: middle;

  &.a-validatorInfo-icon-approved {
    color: $green;
  }
}


/*------------------------------------*\
 * FORM LEGEND
\*------------------------------------*/

.a-legend {
  display: block;
  padding-right: $spacer * 2;
  margin-bottom: $spacer;
  font-family: $font-medium;
  font-size: 20px;
  font-weight: $font-weight-medium;
  color: $blue-darker;
}


/*------------------------------------*\
 * FORM CHECKBOXES
\*------------------------------------*/

.a-form-checkboxes {
  p {
    display: inline;
    margin-left: $spacer;
  }

  .custom-control-indicator {
    width: $spacer * 2;
    height: $spacer * 2;
    background-color: $white;
    border: 2px solid $blue;
    border-radius: 0;
    margin-top: -1px;
  }

  .custom-control {
    display: block;
    margin-bottom: $spacer;
    @include media-breakpoint-up( lg ) {
      display: inline-block;
      margin-right: $spacer * 3;
    }

    &+.custom-control {
      margin-left: 0;
    }

    >input {
      &:checked~.custom-control-indicator {
        background-color: $white;
        background-image: url('../images/checkbox-checked.svg');
        background-size: 100%;
      }

      &:focus~.custom-control-indicator {
        box-shadow: none;
      }
    }
  }

  input {
    &[type='checkbox'],
    &[type='radio'] {
      &:focus+span {
        &:not(.override-focus) {
          border: 2px solid $blue-dark;
        }
      }
    }
  }

  &.custom-controls-stacked {
    .custom-control {
      display: block;
      margin-right: 0;
    }
  }
}

/*------------------------------------*\
 * FORM RADIOBUTTONS
\*------------------------------------*/

.a-radioButtons {
  .custom-control-indicator {
    top: $spacer / 12 * -1;
    width: $spacer * 2;
    height: $spacer * 2;
    background-color: $white;
    border: $spacer / 6 solid $blue;
  }

  .custom-control+.custom-control {
    margin-left: 0;
  }

  .custom-control {
    display: block;
    padding-bottom: $spacer / 3;
    padding-left: $spacer * 3;
    margin-bottom: $spacer;
    @include media-breakpoint-up( lg ) {
      display: inline-block;
      margin-right: $spacer;
    }

    >input {
      &:checked~.custom-control-indicator {
        width: $spacer * 2;
        height: $spacer * 2;
        color: $white;
        background-color: $white;
        border: 2px solid $blue;
      }

      &:focus~.custom-control-indicator {
        box-shadow: none;
      }
    }

    &::after {
      margin-bottom: $spacer !important;
    }
  }

  input {
    &[type='radio'] {
      &:focus+span {
        &:not(.override-focus) {
          border: 2px solid $blue-dark;
        }
      }
    }
  }

  .custom-radio {
    input {
      &:checked~.custom-control-indicator {
        background-image: url('data:image/gif;base64,');
      }
    }
  }
}


/*------------------------------------*\
 * RADIOBUTTONS WITH EXCERPT
\*------------------------------------*/

.a-radioButtons {
  &.a-radioButtons-stackedList {
    .custom-control {
      display: block;
      margin-right: 0;
      margin-bottom: 0;
      @include media-breakpoint-up( lg ) {
        margin-right: 0;
      }
    }
  }
}


/*------------------------------------*\
 * RADIOBUTTONS WITH EXCERPT
\*------------------------------------*/

.a-radioButtons-stackedList {
  .custom-control+.custom-control {
    margin-left: 0;
  }

  .a-legend {
    font-size: 24px;
    color: $black;
  }

  .custom-radio {
    display: block;
    padding: $spacer $spacer * 3;

    &:hover,
    &:active,
    &:focus {
      background: $grey-light;
    }

    .custom-control-indicator {
      top: 13px;
    }

    .a-radioButtons-title {
      display: block;
      padding-bottom: $spacer;
      font-size: 20px;
    }

    .a-radioButtons-excerpt {
      display: block;
    }
  }
}

.a-radioButtons-searchResult {
  .custom-radio {
    &:hover,
    &:focus,
    &:active {
      .a-radioButtons-excerpt {
        color: $grey-medium;
      }
    }

    .a-radioButtons-title {
      color: $grey-medium;

      strong {
        color: $black;
      }
    }

    .a-radioButtons-excerpt {
      color: $grey-medium;
    }
  }

  .custom-control {
    >input {
      &:checked~.a-radioButtons-excerpt {
        color: $black;
      }
    }
  }
}

.a-js-radioParentGray {
  background-color: $grey-light;
}


/*------------------------------------*\
 * Postal code
\*------------------------------------*/

.a-input-postnumber {
  max-width: 100px;
}

.a-input-placecode {
  position: absolute;
  top: $spacer * 3;
  right: -45px;

  &::before {
    content: 'Oslo';
  }
}

#a-js-place {
  display: inline;
}


/*------------------------------------*\
 * FORM SWITCH BUTTONS
\*------------------------------------*/

.a-switch {
  position: relative;
  display: inline-block;
  margin-right: $spacer / 2;
  margin-bottom: $spacer;
  cursor: pointer;

  input {
    &[type='checkbox'],
    &[type='radio'] {
      &+label {
        &.focused {
          // -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #1eaef7;
          // box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px #1eaef7;
          -webkit-box-shadow: 0;
          box-shadow: 0;
        }

        &.disabled {
          cursor: not-allowed;
        }
      }

      &:not(:checked) {
        &+label {
          background: none;
          border: 2px dotted $blue;
          border-radius: $spacer * 3;
          -webkit-border-radius: $spacer * 3 $spacer * 3 $spacer * 3 $spacer * 3;
          transition: all .2s;
        }
      }

      &:checked {
        &+label {
          background-color: rgba( 30, 174, 247, .2 );
          border: 2px solid $blue;
          border-radius: $spacer * 3;
          transition: all .2s;
        }
      }

      &:focus {
        &+label {
          &:not(.override-focus) {
            border-color: $blue-dark;
          }
        }
      }
    }
  }

  .a-switch-label {
    padding: 7px $spacer 5px $spacer;
    font-family: $font-reg;
    font-size: 14px;
    font-weight: $font-weight-reg;
    cursor: pointer;

    &.disabled {
      padding: 8px 18px;
      background-color: $white !important;
      background-image: url('../images/disabled-background.svg') !important;
      border: none !important;
    }
  }

  .a-switch-icon {
    margin-top: $spacer / 4 * -1;
    font-size: 20px;
    vertical-align: middle;
  }
}

// TODO: merge with .a-switch with & revert selector?
fieldset[disabled] .a-switch input[type='checkbox']+label,
fieldset[disabled] .a-switch input[type='radio']+label {
  cursor: not-allowed;
}


/*------------------------------------*\
 * Dropdown
\*------------------------------------*/

.a-input-icon {
  position: absolute;
  top: 7px;
  left: $spacer;
  font-size: 20px;
}

.a-form-text {
  display: block;
  padding: 7px 50px 5px 50px;
}

// Toggle btn
.a-dropdown-toggle {
  width: 100%;
  min-height: $spacer * 3;
  padding: 0;
  text-align: left;
  white-space: normal;
  background-color: $white;
  border: 2px solid $blue;
  border-radius: 0;

  &:hover {
    color: #333;
    border-color: $blue-dark;
  }

  &:focus {
    border-color: $blue-dark;
  }

  &::after {
    position: absolute;
    top: 16px;
    right: $spacer;
  }

  &::before {
    // Making the touch target area bigger without affecting the layout
    position: absolute;
    top: $spacer / 2 * -1;
    left: 0;
    width: 100%;
    height: $spacer * 4;
    content: '';
  }
}

// TODO: merge with .a-dropdown-toggle with & revert selector? .a-dropdown-toggle { .open & {...} }
.open {
  .a-dropdown-toggle {
    border-color: $blue-dark;

    :hover {
      border-color: $blue-dark;
    }
  }
}

// Choises
.a-dropdown-menu {
  width: 100%;
  padding: 0;
  margin-top: 0;
  border: 2px solid $blue-dark;
  border-top: none;
  border-radius: 0;
}

.a-dropdown-item {
  position: relative;
  min-height: $spacer * 3;
  padding: 0;
  white-space: normal;
  border-bottom: 2px dotted $blue-dark;

  &:hover,
  &:focus {
    background-color: $blue-light;
    border-bottom: 2px dotted $blue-dark;
    box-shadow: none !important;
  }

  &:last-child {
    border-bottom: none;
  }
}

.a-dropdown-item-active {
  background-color: $grey-light;
  border-bottom: 2px dotted $blue-dark;
  box-shadow: none !important;

  &:hover {
    background-color: $grey-light;
  }
}

/*------------------------------------*\
 * SELECT MENU
\*------------------------------------*/

.a-custom-select {
  height: $spacer * 3;
  padding: 0 $spacer * 3 0 $spacer * 2;
  color: $black;
  cursor: pointer;
  background: $white url('data:image/svg+xml;base64,') no-repeat right .75rem center;

  border: 2px solid $blue;
  border-radius: 0;

  &:hover {
    background-color: $white;
    border: 2px solid $blue-dark;
  }

  &:focus {
    border: 2px solid $blue-dark;
    box-shadow: none !important;
  }
}
