/*------------------------------------*\
    $MODAL
\*------------------------------------*/

.a-page {
  .a-modal {
    min-height: 100vh;
  }
}

.a-modal {
  background: $blue;
  padding-bottom: $spacer*5;
  .container {
    @include media-breakpoint-down(lg) {
      padding: 0;
    }
  }
  .modal-lg {
    width: auto;
    max-width: none;
    margin: 0 $spacer;
    @media screen and (min-width: 920px) {
      margin: auto;
    }
  }
  .modal-content {
    margin-bottom: $spacer*5;
    border: none;
    border-radius: 0;
    box-shadow: 1px 1px 4px 0px rgba(137, 137, 137, 0.50);
    // box-shadow: 0 1px 5px -2px #000; // Current
  }
  .a-link-helpfunction {
    color: $white;
    a,
    .a-link-chat {
      color: $white;
      border-color: $white;
    }
  }
  .a-modal-contentFullWidth {
    width: calc(100% + 47px);
    margin-left: -$spacer*2;
    .a-legend {
      padding: 0 $spacer*2;
    }
    .custom-radio {
      padding: $spacer $spacer $spacer $spacer*5;
    }
    .custom-control-indicator {
      left: $spacer*2;
    }
    @include media-breakpoint-up(md) {
      width: calc(100% + 168px);
      margin-left: -84px;
      .a-legend {
        padding: 0 84px;
      }
      .custom-radio {
        padding: $spacer $spacer*10;
      }
      .custom-control-indicator {
        left: 84px;
      }
    }
    @include media-breakpoint-up(lg) {
      width: calc(100% + 191px);
      margin-left: -95px;
      .a-legend {
        padding: 0 96px;
      }
      .custom-radio {
        padding: $spacer $spacer*11;
      }
      .custom-control-indicator {
        left: 96px;
      }
    }
  }
  .a-link-helpfunction {
    text-align: center;
  }
  .a-personSwitcher-name {
    display: inline-block;
    max-width: 160px;
  }
}

// Modal top
.a-modal-top {
  min-height: $spacer*4;
  padding-top: $spacer*3;
  .a-logo {
    max-width: 110px;
    float: left;
    margin-right: $spacer;
    @include media-breakpoint-up(lg) {
      max-width: 142px;
      margin-top: 2px;
    }
  }
  .a-modal-top-user {
    float: right;
    position: relative;
    .a-personSwitcher {
      color: #fff;
      padding: 0;
      margin-right: -3px;
      width: 200px;
      margin-top: -2px;
      @include media-breakpoint-up(lg) {
        margin-top: 2px;
      }
    }
    .a-personSwitcher-icon {
      border: 1px solid $white;
    }
  }
}

.a-modal-navbar {
  margin-top: 36px;
  height: 48px;
}

.a-modal-header {
  padding: 0px;
}

.a-modal-title {
  font-size: $font-size-large-2;
  color: #fff;
  width: 100%;
  font-size: 18px;
  line-height: 24px;
  position: relative;
  min-height: 96px;
  display: table-cell;
  margin: 0;
  vertical-align: middle;
  @include media-breakpoint-up(md) {
    font-size: 24px;
  }
}

.a-modal-body {
  padding: $spacer*2;
  @include media-breakpoint-up(md) {
    padding: 36px 84px;
  }
  @include media-breakpoint-up(lg) {
    padding: $spacer * 3 $spacer * 8;
  }
  .a-btn-group {
    margin-top: $spacer*3;
  }
}

.a-modal-close,
.a-modal-back {
  border: 0;
  background: $white;
  border-radius: 50%;
  padding: 0 6px;
  height: 36px;
  margin-bottom: 12px;
}

.a-modal-close:hover,
.a-modal-back:hover {
  opacity: .70;
  border: none;
}

.a-modal-close:focus,
.a-modal-back:focus {
  background: $blue-light;
  border: none;
}

.a-modal-close {
  float: right;
}

a.a-modal-close {
  text-align: center;
  padding-top: 6px;
}

.a-modal-close-icon,
.a-modal-back-icon {
  vertical-align: middle;
  cursor: pointer;
}

.a-modal-close-icon {
  color: $red;
}

.a-modal-footer {
  padding: 0px $spacer*8 $spacer*3 $spacer*8;
}


/*------------------------------------*\
    $MODAL PROCESS
\*------------------------------------*/

// MODAL WITH SUCCESS
.a-modal-background-success {
  background: $green-light;
  .a-modal-top {
    .a-personSwitcher {
      color: $black;
    }
  }
  .a-iconText-background {
    background: $green;
    .a-iconText-text  {
      color: $black;
    }
  }
  .a-link-helpfunction {
    color: $black;
    a {
      color: $black;
      border-color: $black;
    }
  }
}

// MODAL WITH ERROR
.a-modal-background-error {
  background: $red-light;
  .a-modal-top {
    .a-personSwitcher {
      color: $black;
    }
  }
  .a-iconText-background {
    background: $red;
    .a-iconText-text {
      color: $black;
    }
  }
  .a-link-helpfunction {
    color: $black;
    a {
      color: $black;
      border-color: $black;
    }
  }
}


/*------------------------------------*\
    $MODAL table segment done
\*------------------------------------*/

.modal-table-segment {
  background-color: $green-light;
  height: 200px;
  width: 100%;
}

.segment-fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
}

.modal-table-segment-box {
  height:80px;
}

.segment-done {
  display: none;
}

.a-collapseContent {
  .within-table {
    margin-left: -$spacer;
    margin-right: -$spacer;
    margin-bottom: -$spacer;
  }

  .modal-table-segment {
    height: $spacer * 7;
  }

  .modal-table-segment-box {
    display: none;
  }
}
