/*------------------------------------*\
    $MIXINS
\*------------------------------------*/


/* CSS Transition
	Usage: @include transition(width,0.3s,ease-out);
 */

@mixin transition($transition-property, $transition-time, $method) {
  -webkit-transition: $transition-property $transition-time $method;
  -moz-transition: $transition-property $transition-time $method;
  -ms-transition: $transition-property $transition-time $method;
  -o-transition: $transition-property $transition-time $method;
  transition: $transition-property $transition-time $method;
}


/* Rem Unit font sizes with relative fallback http:/seesparkbox.com/foundry/scss_rem_mixin_now_with_a_better_fallback
	Usage: @include font-size(1, large);
*/

@mixin font-size( $decimal-size, $keyword: null) {
  @if $keyword {
    font-size: $keyword;
  }
  @else {
    font-size: $decimal-size * $base-font-multiplier * 16px;
  }
  font-size: $decimal-size * 1rem;
}


/* Decide responsive width of container */

@mixin make-container-widths( $widths: $container-widths ) {
  @each $breakpoint,
  $container-width in $widths {
    @include media-breakpoint-up( $breakpoint ) {
      width: $container-width;
    }
  }
}

/*@mixin disabled-background( $color: $black, $background-color: $white, $box-shadow: 0 0 0 1px $white inset, $background-image... ) {
  color: $color;
  cursor: not-allowed;
  background-color: $background-color;
  background-image: $background-image;
  background-repeat: repeat;
  background-size: auto;
  box-shadow: $box-shadow;
}*/

@mixin disabled-background( $color: $black, $background-color: $white, $box-shadow: 0 0 0 1px $white inset, $background-image: url( '../images/disabled-background.svg' ), $background-image-extra: null ) {
  color: $color;
  cursor: not-allowed;
  background-color: $background-color;
  background-image: $background-image, $background-image-extra;
  background-repeat: repeat;
  background-size: auto;
  box-shadow: $box-shadow;
}