/* List in textarticle */

.a-article-right-off {
  @include media-breakpoint-up( md ) {
    float: right;
    max-width: 65%;
    margin-left: 24px;
  }
  @include media-breakpoint-up( lg ) {
    max-width: 75%;
    margin-right: -192px;
  }
}


// Content in article

.a-text {
  h1,
  h2,
  h3,
  h4 {
    margin: $spacer*2 0 $spacer 0;

    @include media-breakpoint-up( md ) {
      margin: $spacer * 2 0 $spacer 0;
    }
    @include media-breakpoint-up( lg ) {
      margin: $spacer * 3 0 $spacer 0;
    }
  }

  h3,
  h4 {
    margin: $spacer * 2 0 $spacer / 2 0;
  }

  h2 + h3 {
    margin-top: 12px !important;
  }

  ul {
    list-style: disc;

    &.a-icon-list {
      li {
        margin-bottom: $spacer * 1.5;
      }
    }

    &.connected-bullets {
      margin-left: 0px;
      li {
        margin: 0;
      }
    }

    &.a-bullet-list {
      margin-top: $spacer;
      margin-left: 0;
      list-style: none;

      @include media-breakpoint-up( md ) {
        margin-left: -$spacer * 2;
      }
    }

    ul {
      margin-top: 10px;
      margin-bottom: 0;
    }
  }

  ul,
  ol {
    margin: 0 0 $spacer * 3 $spacer * 2;

    &.no-declaration {
      margin: 0;
      list-style: none;

      li {
        margin: 0;
      }
    }
  }

  ol {
    &.a-circle-list {
      li {
        margin-bottom: $spacer * 2;
      }
    }

    ol {
      margin-top: 10px;
      margin-bottom: 0;
    }
  }

  li {
    margin-bottom: 10px;
  }

  ul.no-decoration {
    margin: 0;
    list-style: none;
  }
}




// Image in articlelist

.a-articleImage-right {
    float: right;
    margin-right: -96px;
    max-width: 56%;
    padding: 6px 0 24px 24px;
}

.a-articleImage-left {
    float: left;
    margin-left: -96px;
    max-width: 56%;
    padding: 6px 24px 24px 0;
}

// Box
.a-box {
  float: left;
  width: calc( 100% + 48px );
  padding: $spacer * 2 $spacer * 2;
  margin-left: -24px;
  background: $white;
  border: 12px solid $blue-light;

  @include media-breakpoint-up( sm ) {
    width: 100%;
    padding: $spacer * 4 $spacer * 4;
    margin-left: auto;
  }
  @include media-breakpoint-up( md ) {
    padding: $spacer * 4 $spacer * 7;
  }

  .a-box-heading {
    float: left;
    padding-bottom: $spacer * 2;
  }

  .a-box-body {
    float: left;
  }

  .a-box-footer {
    float: left;
    padding-top: $spacer;
  }

  &.a-article-list-dates {
    @include media-breakpoint-up( md ) {
      .a-box-heading,
      .a-box-footer {
        margin-left: $spacer * 8; // When a-box is used for date list, push heading and footer
      }
    }
  }
}

// Article teaser
.a-article-teaser {
  float: left;
  width: 100%;
  margin-bottom: $spacer;

  a {
    display: block;

    :hover,
    :focus {
      .a-date {
        background: $blue-dark;
      }
    }
  }
}

.a-article-teaser-text {
  width: calc( 100% - 60px );

  @include media-breakpoint-up( sm ) {
    float: left;
    padding-left: $spacer * 3;
  }
  @include media-breakpoint-up( md ) {
    margin-top: -2px;
  }
  @include media-breakpoint-up( lg ) {
    margin-top: -5px;
  }
  @include media-breakpoint-up( xl ) {
    margin-top: -8px;
  }

  h2 {
    margin-bottom: 0;
    font-family: $font-bold;
    font-size: 20px;
    font-weight: $font-weight-bold;
  }
}

// Byline
.a-byline {
  margin-top: -$spacer / 2;
  margin-bottom: $spacer * 2;
}

// Date
.a-date {
  width: $spacer * 5;
  height: $spacer * 5;
  margin-bottom: $spacer;
  color: $white;
  text-align: center;
  background: $blue;

  @include media-breakpoint-up( sm ) {
    float: left;
    margin-bottom: $spacer * 3;
  }

  .a-date-day {
    padding-top: 5px;
    font-size: $font-size-large-2;
  }

  .a-date-month {
    margin-top: -5px;
    font-size: $font-size-small;
  }
}
