// Bootstrap overrides
//
// Copy variables from `_variables.scss` to this file to override default values
// without modifying source files.
// Colors
//
// Grayscale and brand colors for use across Bootstrap.
$gray-dark: $grey;
$gray: $grey;
$gray-light: $grey-light;
$gray-lighter: $grey-lighter;
$gray-lightest: $grey-lighter;
$brand-primary: $blue;
$brand-success: $green;
$brand-info: $blue;
$brand-warning: $red;
$brand-danger: $red;
// Options
//
// Quickly modify global styling by enabling or disabling optional features.
$enable-flex: true !default;
$enable-rounded: true !default;
$enable-shadows: false !default;
$enable-gradients: false !default;
$enable-transitions: false !default;
$enable-hover-media-query: false !default;
$enable-grid-classes: true !default;
// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
$spacer: 12px;
$spacer-x: $spacer !default;
$spacer-y: $spacer !default;
$spacers: ( 0: ( x: 0, y: 0), 1: ( x: $spacer-x, y: $spacer-y), 1o5: ( x: ($spacer-x * 1.5), y: ($spacer-y * 1.5)), 2: ( x: ($spacer-x * 2), y: ($spacer-y * 2)), 3: ( x: ($spacer-x * 3), y: ($spacer-y * 3)), 4: ( x: ($spacer-x * 4), y: ($spacer-y * 4)), 5: ( x: ($spacer-x * 5), y: ($spacer-y * 5)), 6: ( x: ($spacer-x * 6), y: ($spacer-y * 6)), 7: ( x: ($spacer-x * 7), y: ($spacer-y * 7)), 8: ( x: ($spacer-x * 8), y: ($spacer-y * 8))) !default;
$border-width: 1px !default;
// Body
//
// Settings for the `<body>` element.
$body-bg: #fff !default;
$body-color: $gray-dark !default;
// Links
//
// Style anchor elements.
$link-color: $brand-primary !default;
$link-decoration: none !default;
$link-hover-color: darken($link-color, 15%) !default;
$link-hover-decoration: underline !default;
// Grid breakpoints
//
// Define the minimum and maximum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.
$grid-breakpoints: ( // Extra small screen / phone
xs: 0, // Small screen / phone
sm: 544px, // Medium screen / tablet
md: 768px, // Large screen / desktop
lg: 992px, // Extra large screen / wide desktop
xl: 1200px) !default;
// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.
$container-max-widths: ( sm: 576px, md: 720px, lg: 940px, xl: 1140px) !default;
// Grid columns
//
// Set the number of columns and specify the width of the gutters.
$grid-columns: 12 !default;
$grid-gutter-width: 48px;
// Typography
//
// Font, line-height, and color for body text, headings, and more.
$font-family-sans-serif: "Helvetica Neue",
Helvetica,
Arial,
sans-serif !default;
$font-family-serif: Georgia,
"Times New Roman",
Times,
serif !default;
$font-family-monospace: Menlo,
Monaco,
Consolas,
"Courier New",
monospace !default;
$font-family-base: $font-family-sans-serif !default;
// Pixel value used to responsively scale all typography. Applied to the `<html>` element.
$font-size-root: 16px !default;
$font-size-base: 1rem !default;
$font-size-lg: 1.25rem !default;
$font-size-sm: .875rem !default;
$font-size-xs: .75rem !default;
$font-size-h1: 2.5rem !default;
$font-size-h2: 2rem !default;
